import React, {useState,useEffect} from 'react';
import { Form, Card, Image, Icon, Header, List, Message, Button} from 'semantic-ui-react';
import logo from './logo.svg';
import './App.css';

function App() {
  const [name, setName] = useState('');
  const [userName, setUsername] = useState('');
  const [followers,setFollowers] = useState('');
  const [following, setFollowing] = useState('');
  const [repos, setRepos] = useState('');
  const [avatar,setAvatar] = useState('');
  const [userInput, setUserInput] = useState('');
  const [repoData, setRepoData] = useState('');
  const [error, setError] = useState(null);

  useEffect(() => {
    fetch('https://api.github.com/users/zhengzhe46')
      .then((res) => res.json())
      .then(data => {
        setData(data);
      });
  },[]);

  const setData = ({name, login, followers, following, public_repos, avatar_url}) => {
    setName(name);
    setUsername(login);
    setFollowers(followers);
    setFollowing(following);
    setRepos(public_repos);
    setAvatar(avatar_url);
  };

  const handleSearch = (e) => {
    setUserInput(e.target.value);
  };

  const handleSubmit = () => {
    fetch(`https://api.github.com/users/${userInput}`)
      .then((res)  => res.json())
      .then(data => {
        if(data.message){
          setError(data.message);
        }
        else{
          setData(data);
          setError(null);
        }
      });
  };
  
  async function repoDataURL(){
    await fetch(`https://api.github.com/users/${userInput}/repos`)
      .then((res) => res.json())
      .then(
        (result) => {
          console.log=result.map((item) => (
            <div className='text-center'>
              <a target="_blank" href={item.svn_url}>
                {item.name}
              </a>
            </div>
          ));
          setRepoData(List);
        },
        (error) => {
          console.log(error);

        }
      );
  }


  return (
    <div>
      <div className='navbar'>
        <Header as='h2'>
          <Image circular src={avatar} />{userName}
        </Header>
      </div>
      <div className='search'>
        <Form onSubmit={handleSubmit}>
          <Form.Group>
            <Form.Input
              placeholder='Github user'
              name='github user'
              onChange={handleSearch}
            />
            <Form.Button content='查詢' />
          </Form.Group>
        </Form>
      </div>
      { error ?(<div className="error">
        <Message negative>
          <Message.Header><h1>無用戶資訊</h1></Message.Header>
          <p>(請您輸入正確用戶賬號)</p>
        </Message>
      </div>
      ):(<dic className="card">
        <Card>
          <Image src={avatar} wrapped ui={false} />
          <Card.Content>
            <Card.Header>{name}</Card.Header>
            <Card.Header>{userName}</Card.Header>
          </Card.Content>
          <Card.Content extra>
            <a>
              <Icon name='user' />
              {followers} 追隨者(Followers)
            </a>
          </Card.Content>
          <Card.Content extra>
            <a>
              <Icon name='user' />
              {repos} 倉儲(Repos)
            </a>
          </Card.Content>
          <Card.Content extra>
            <a>
              <Icon name='user' />
              {following} 關注者(Following)
            </a>
          </Card.Content>
        </Card> 
            
      </dic>)}
      <Button onClick={repoDataURL}>Click Here</Button>
    </div>
    
  );
}

export default App;
